body {
  margin: 0%;
  /* border: 1px solid; */
  height: 100vh;
  background-color: gold;
  /* box-sizing: border-box; */
}

.top-container {
  border: 2px solid;
  height: 10vh;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  background-color: white;

}

.bottom-container {
  /* border: 1px solid; */
  height: 90vh;
  box-sizing: border-box;
  background-color: re;
}

.content-box {
  border: 8px solid black;
  height: 50%;
  width: 50%;
  margin-top: 10%;
  margin-left: 25%;
  /* background: lightcyan; */
  box-sizing: border-box;
  border-radius: 5px;
}

.content-top {
  border: 1px solid;
  height: 85%;
  box-sizing: border-box;
}

.content-bottom {
  border: 1px solid;
  height: 15%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  background-color: gold;
}

.button {
  background: black;
  color: white;
  border-radius: 5px;
  font-size: 11px;
  margin-top: 10px;
}

.title {
  text-align: center;
  border: 1px solid;
  height: 30%;
  box-sizing: border-box;
  display: flex;
  font-weight: bolder;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: gold;
}

.body-content {
  text-align: center;
  border: 1px solid;
  height: 70%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: white;
}

.search {
  margin-right: 25px;
  background: gold;
  color: black;
  border-radius: 5px;
}

.delete-content {
  font-size: 13px;
}

.id-color {
  text-align: left;
  color: white;
  background-color: black;
}

.heading {
  margin-left: 25px;
  background: gold;
  /* border: 1px black; */
  font-size: x-large;
  font-family: cursive;
}

.comment-type {
  margin-top: 10px;
  color: black;
  border-radius: 5px;
  border: 1px solid black;
}

.submit-comment {
  margin-top: 10px;
  color: black;
  border-radius: 5px;
  font-weight: 900;
  background-color: white;
}

.comment-container {
  /* border: 1px solid; */
  height: 20%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.comment {
  /* border: 1px solid; */
  box-sizing: border-box;
  height: 10%;
  padding-left: 10px;
}